<template>
  <div class="taskSubmit">

    <van-row class="navRow">
      <van-col span="2" offset="1">
        <van-icon name="arrow-left" size="20px" class="nav_icon" @click="goBack"/>
      </van-col>
      <van-col span="18" class="nav_title">
        <span></span>
      </van-col>
    </van-row>
    <div class="nav_block"></div>

    <van-row>
      <van-col span="22" offset="1">
        <van-row>
          <van-col span="24" class="title">
            
          </van-col>
          <van-col span="24" class="content">
            {{taskDetails.task.take_care}}
          </van-col>
        </van-row>

        <van-row>
          <van-col span="24" class="title">
            
          </van-col>
          <van-col span="24" class="content">
            {{taskDetails.task.submit_notice}}
          </van-col>
        </van-row>

        <van-row v-if="taskDetails.task.submit_way == 2">
          <van-col span="24" class="title">
            
          </van-col>
          <van-col span="6" class="submit_img" v-for="(img, imgIndex) in taskDetails.task.submit_img" :key="imgIndex">
            <img :src="img"  @click="subImgView(imgIndex)"/>
          </van-col>
        </van-row>

        <van-row v-if="taskDetails.task.submit_way == 2">
          <van-col span="24" class="title">
            
          </van-col>
          <van-col span="6" class="upload_img" v-for="(item,index) in formItem.submit_img" :key="index">
            <img class="image" :src="item" @click="imgView(index)">
            <van-icon name="clear" size="20px" color="#FF0000" class="close_icon" @click="imgclose(index)"/>
          </van-col>
          <van-col span="6" class="upload_img">
            <van-uploader :after-read="onRead">
              <van-icon name="plus" size="30px" color="#00BCD4" class="add_icon"/>
            </van-uploader>
          </van-col>
          <van-col span="6" class="upload_img">
            <!--<input style="width: 20px; height: 20px; background-color: #333;" type="file" @click="upFile"/>-->
              <van-icon name="plus" size="30px" color="#00BCD4" class="add_icon" @click="upImg"/>
          </van-col>
        </van-row>

        <van-row>
          <van-col span="24" class="title">
            
          </van-col>
          <van-col span="24" class="text_area">
            <van-field :placeholder="taskDetails.task.submit_notice" v-model="formItem.submit_text" type="textarea" @input="inputText"/>
          </van-col>
        </van-row>

      </van-col>
    </van-row>
    <div class="block"></div>

    <!--receiveTask-->
    <van-row class="receive_row">
      <van-col span="22" offset="1">
        <van-button class="submit_data" @click="submitData" v-if="taskDetails.status == 0">
          <span class="time">
            <van-icon name="underway-o" class="clock"/>{{countTime}}
          </span>
        </van-button>
        <van-button class="submit_data" v-if="taskDetails.status == 1">
          <span class="time">
            <van-icon name="underway-o" class="clock"/>{{countTime}}
          </span>
        </van-button>
        <van-button class="submit_data" v-if="taskDetails.status == 2"></van-button>
        <van-button class="submit_data" v-if="taskDetails.status == 3"></van-button>
        <van-button class="submit_data" v-if="taskDetails.status == 4"></van-button>
      </van-col>
    </van-row>

    <van-popup v-model="imgShow" class="show_img">
      <img :src="showImg" @click="hideImg">
    </van-popup>
  </div>
</template>

<script>
  import { Toast } from 'vant';
  import Input from "../../../www_tmp/YXJ-StoreWeb/build/vendors";
  export default {
    name: 'TaskSubmit',
    components: {Input},
    data () {
      return {
        id: this.$route.params.id,
        taskDetails: {
          task: {
            take_care: ''
          }
        },
        formItem: {
          id: this.$route.params.id,
          submit_img: [],
          submit_text: ''
        },
        areaPlaceholder: '1\n' +
          '2\n' +
          '3\n' +
          '+',
        countTime: '--:--:--',
        timer: '',
        imgShow: false,
        showImg: '',
        localIds: '',
        imgBase64: ''
      }
    },
    created() {
      this.init()
    },
    methods: {
      init () {
        this.getTaskDetails()
      },
      goBack () {
        this.$router.back({
          params: { task_id: this.taskDetails.task.task_id }
        })
      },
      getTaskDetails () {
        Toast.loading({
          mask: true,
          message: '...',
          duration: 0
        });
        let vm = this
        this.axios.post(this.apiList.apiUserTaskDetails,{id: vm.id},{
          headers: {
            'token': localStorage.getItem('token')
          }
        }).then(function (res) {
          if (res.data.code === 1) {
            console.log(res)
            vm.taskDetails = res.data.data
            // 
            if (vm.taskDetails.status == 0 || vm.taskDetails.status == 1) {
              if (vm.taskDetails.surplus_time > 0) {
                vm.resetTime(vm.taskDetails.surplus_time)
              }else{
                vm.getTaskDetails()
              }
            }
            Toast.clear()
          }
        })
      },
      onRead (file) {
        Toast.loading({
          mask: true,
          message: '...',
          duration: 0
        });
        // 1.5MBjpegpng
        if(file.file.size>100000) {
          // Canvas()
          let canvas =  document.createElement('canvas')
          // CanvasRenderingContext2D()
          let context = canvas.getContext('2d')
          // 
          let img = new Image()
          // DataURL(base64)
          img.src = file.content
          // 
          img.onload = () => {
            // canvas
            canvas.width = 400
            canvas.height = 300
            /* drawImage(0,0)Canvas400300
            */
            context.drawImage(img, 0, 0, 400, 300)
            // base64file.file.type0.92
            file.content = canvas.toDataURL(file.file.type, 0.1)
            // base64
            // this.imgBase64.push(file.content)
          }

        }

        //formnew FormData
        let fd = new FormData()
        if (file && file.length) { //  
          file.forEach(item => {
            fd.append("file", item.file)//file
          })
        } else {
          fd.append("file", file.file)
        }
        this.axios.post(this.apiList.apiUpload, fd) //url
        //
          .then(res => {
            Toast.clear()
            //img
            this.formItem.submit_img = this.formItem.submit_img.concat(res.data.data.fileUrl)
            Toast('')
          })
      },
      upImg () {
        let vm =this
        wx.chooseImage({
          count: 9, // 9
          sizeType: ['original', 'compressed'], // 
          sourceType: ['album'], // 
          success: function (res) {
            console.log(res)
            let localIds = res.localIds // IDlocalIdimgsrc
            // vm.formItem.submit_img = vm.formItem.submit_img.concat(res.localIds);
            // console.log(vm.formItem.submit_img)
            // Toast.loading({
            //   mask: true,
            //   message: '...',
            //   duration: 0
            // });
            for (let i=0;i<localIds.length;i++) {
              wx.getLocalImgData({
                localId: localIds[i], // localID
                success: function (res) {
                  Toast('')
                  // var localData = res.localData; // localDatabase64img
                  // vm.formItem.submit_img = vm.formItem.submit_img.concat(res.localData);
                  // vm.formItem.submit_img.push(res.localData);

                  /*//formnew FormData
                  let fd = new FormData()
                  fd.append("file", res.localData)
                  vm.axios.post(vm.apiList.apiUpload, fd).then(res => {
                    //img
                    // this.formItem.submit_img = this.formItem.submit_img.concat(res.data.data.fileUrl)
                    vm.formItem.submit_img.push(res.data.data.fileUrl);
                  })*/
                }
              });
            }
            // Toast.clear()
            // Toast('')
          }
        })
      },
      upFile (file) {
        console.log(file)
        Toast.loading({
          mask: true,
          message: '...',
          duration: 0
        });
        //formnew FormData
        let fd = new FormData()
        if (file && file.length) { //  
          file.forEach(item => {
            fd.append("file", item.file)//file
          })
        } else {
          fd.append("file", file.file)
        }
        this.axios.post(this.apiList.apiUpload, fd) //url
        //
          .then(res => {
            Toast.clear()
            //img
            this.formItem.submit_img = this.formItem.submit_img.concat(res.data.data.fileUrl)
            Toast('')
          })
      },
      //
      imgclose (index) {
        this.formItem.submit_img.splice(index, 1)
        Toast('')
      },
      //
      subImgView (index) {
        this.showImg = this.taskDetails.task.submit_img[index]
        this.imgShow = true
      },
      //
      imgView (index) {
        this.showImg = this.formItem.submit_img[index]
        this.imgShow = true
      },
      //
      hideImg () {
        this.imgShow = false
      },
      inputText (res) {
        console.log(res)
      },
      submitData () {
        Toast.loading({
          mask: true,
          message: '...',
          duration: 0
        });
        let vm = this
        this.axios.post(this.apiList.apiSubmitTask, vm.formItem, {
          headers: {
            'token': localStorage.getItem('token')
          }
        }).then(function (res) {
          if (res.data.code === 1) {
            Toast.clear()
            vm.$router.push({
              name: 'SubmitSuccess'
            })
          }else{
            Toast(res.data.msg)
          }
        })
      },
      resetTime(hour) {
        let vm = this
        this.timer = setInterval(() => {
          hour -= 1000;
          let h = Math.floor(hour / (1000 * 60 * 60));
          let m = Math.floor((hour % (1000 * 60 * 60)) / (1000 * 60));
          let s = Math.floor((hour % (1000 * 60)) / 1000);
          if (h == 0 && m == 0 && s == 0) {
            clearInterval(vm.timer);
            vm.getTaskDetails()
            /*if(vm.taskDetails.status == 0){
              vm.taskDetails.status = 4
            }else if(vm.taskDetails.status == 1){
              vm.taskDetails.status = 2
            }
            this.axios.post(this.apiList.apiUserTaskDetails, {id: vm.id}, {
              headers: {
                'token': localStorage.getItem('token')
              }
            }).then(function (res) {
              if (res.data.code === 1) {

              }
            })*/
          }
          // if (h < 10) {
          //   h = "0" + h;
          // }
          if (m < 10) {
            m = "0" + m;
          }
          if (s < 10) {
            s = "0" + s;
          }
          vm.countTime = h + ":" + m + ":" + s;
        }, 1000);
      }
    },
    onHide:function() {
      clearInterval(this.timer);
    },
    onUnload(){
      clearInterval(this.timer);
    }
  }
</script>
<style>
  .nav_block{
    height: 40px;
  }
  .block{
    height: 60px;
  }
</style>
<style scoped>
  .navRow{
    position: fixed;
    top: 0;
    width: 100%;
    height: 40px;
    background-color: #3f3f3f;
    color: #fff;
    font-size: 16px;
    line-height: 40px;
  }
  .navRow .nav_icon{
    float: left;
    line-height: 40px;
  }
  .navRow .nav_title{
    text-align: center;
  }
  .taskSubmit .title{
    margin: 15px 0 8px;
    font-weight: 600;
    font-size: 18px;
    color: #555;
    word-wrap: break-word;
    word-break: normal;
  }
  .taskSubmit .content{
    font-size: 14px;
    line-height: 22px;
    color: #555;
    word-wrap: break-word;
    word-break: normal;
  }
  .taskSubmit .submit_img{
    text-align: center;
  }
  .taskSubmit .submit_img img{
    width: 60px;
    height: 80px;
    border: 1px solid #00BCD4;
    border-radius: 4px;
  }
  .taskSubmit .upload_img{
    position: relative;
    height: 80px;
    text-align: center;
    /*background-color: #ccc;*/
  }
  .taskSubmit .upload_img img{
    width: 60px;
    height: 80px;
    border: 1px solid #00BCD4;
    border-radius: 4px;
  }
  .taskSubmit .upload_img .add_icon{
    /*margin: 25px 10px;*/
    border: 1px solid #00BCD4;
    border-radius: 4px;
    width: 60px;
    height: 80px;
    line-height: 82px;
  }
  .taskSubmit .upload_img .close_icon{
    position: absolute;
    top: 0;
    right: 0;
  }
  .taskSubmit .text_area{
    /*height: 100px!important;*/
    border-radius: 2px;
    border: 1px solid #ccc;
  }
  .taskSubmit .receive_row{
    position: fixed;
    width: 100%;
    bottom: 0;
    padding: 5px 0 10px;
    background-color: #fff;
  }
  .taskSubmit .receive_row button{
    position: relative;
    width: 100%;
    background-color: #00BCD4;
    border-radius: 4px;
    color: #fff;
    font-size: 18px;
  }
  .taskSubmit .receive_row .submit_data .time{
    position: absolute;
    display: block;
    height: 30px;
    padding: 0 10px;
    line-height: 30px;
    border: 1px solid #fff;
    border-right: none;
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
    background-color: blue;
    bottom: 5px;
    right: 0;
  }
  .taskSubmit .receive_row .submit_data .time .clock{
    float: left;
    margin: 6px 4px 0 0;
  }
  .taskSubmit .show_img {
    width: 90%;
    text-align: center;
  }
  .taskSubmit .show_img img {
    max-width: 100%;
    max-height: 90%;
  }
</style>



